.add-task-button {
  background-color: chocolate;
  height: 40px;
  padding: 0 10px;
  border-radius: 5px;
  width: 100%;
  font-size: 16px;
  color: #444;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 200ms linear;
}

.add-task-button:hover {
  background-color: #444;
  color: chocolate;
}

.add-task-button-container {
  margin-left: 10px;
  flex: 1;
}
